.toolbar{
    position:fixed;
    top:0;
    z-index: 2;
    width:100%;
    /* height:3rem; */
    background-color:black;
    display: flex;
    align-items: center;
}

.toolbar img {
    height:2.7rem;
    width:5rem;
    margin:0.8rem;
    /* filter: brightness(30%); */
}

.link_style {
    text-decoration: none;
    color:white;
    font-weight: bold;
    font-size: 1.2rem;
    padding:0 2rem;
}

.p_link_style {
    text-decoration: none;
    color:white;
    font-weight: bold;
    font-size: 1.2rem;
    padding:0 2rem;
    display:inline-block;
    cursor:pointer;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}




.link_style:hover{
    color:lightsteelblue
}

.bars_container {
    position: absolute; 
    right: 3rem;
    /* max-width:30%; */
}


.bar1, .bar2, .bar3 {
    width: 25px;
    height: 3.5px;
    background-color: white;
    margin: 6px 0;
    transition: 0.4s;
} 




