.form_container {
    text-align:center; 
    top:0;
    background-color: rgb(219, 219, 219);
    height:60%;
    width:30%;
    position: fixed;
    z-index: 3;
    top:20%;
    left:35%;
}

.form_container form{
    padding:5rem 2rem;
    width:60%;
    margin:auto;
}

#form_title {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 2.5rem;
    margin:2rem 0 0 0 ;
}
/* 
ul{
    align-items: center;
    justify-content: center;
    padding-left:0;
    margin:auto;
    width:70%;
} */


.form_container input{
    text-align: center;
    box-sizing:border-box;
    width:100%;
    border: none;
    height:2rem;
    border-radius: 0.5rem;
    cursor:default;
    margin:0.7rem 0;
}

input:focus {
    outline: none;
}

.password_ret_link{
    color: rgb(73, 62, 62);
    text-decoration: none;
}

.form_container .button {
    font:inherit;
    font-weight: bold;
    background-color: rgb(151, 151, 252);
    color:white;
    width:100%;
    height:2rem;
    border-radius: 0.3rem;
    border:none;
    cursor: pointer;
    padding:0.2rem;
    margin:1.5rem 0;
}

.button:focus{
    outline: none;
}

.form_container .invalid_input {
    border: 3px solid red;
}


