.running_workout_container {
    display: grid;
    background:rgba(0,0,0,0.8);
    grid-template-columns: 65% 1fr;
    grid-template-rows: 10rem 1fr;
}

.custom-select {
    padding:2rem;
    height:4rem;
}

.custom-select select{
    width:30%;
    -webkit-appearance: none;
    -ms-appearance: none; 
    outline:none;
    display: inline-block;
    margin:2rem 4rem;
    height:2rem;
    border-radius: 5px;
    font-size:1.1rem;
    padding:0.3rem;
}

.running_content{
    /* height: 100%; */
    /* height: 30rem; */
    border:5px solid white;
    color:white;
    grid-row-start: 2;
    grid-row-end: 3;
    min-width: 22rem;
    text-align: center;
    margin:2rem 0;
}

